@import url('https://fonts.googleapis.com/css?family=Roboto:300,400,500&display=swap');

* {
  padding: 0;
  outline: 0;
  box-sizing: border-box;
  margin: 0;
}
body, input, button {
  font-family: Roboto, Helvetica, Arial, sans-serif;
  color: #262626;
  font-size: 14px;
  line-height: 20px;
  background: #fafafa;
}
a {
  text-decoration: none;
  transition: all 150ms linear 0s;
  color: #000;
}
img {
  max-width: 100%;
}
:root {
  --primary: #999
}

.top-info {
  display: flex;
   align-items: center;
 }
 
 .top-info span {
   display: flex;
   align-items: center;
 }
 
 .top-info svg {
   font-size: 25px;
   margin: 0 10px;
 }
 
 .top-info span + span {
   margin-left: 10px;
   padding-left: 10px;
   border-left: 1px solid rgba(0, 0, 0, 0.175);
 }

 .home {
  max-width: 1000px;
  margin: 30px auto;
  display: grid;
  grid-template-columns: 2fr 1fr;
  grid-gap: 20px;
}

.profile {
  max-width: 1000px;
  margin: 30px auto;

  display: flex;
}

.profile ul {
  list-style: none;
}

.profile .info {
  margin-left: 20px;
}

.header {
  border-bottom: 1px solid rgba(0, 0, 0, 0.0975);
  width: 100%;
  background-color: #fff;
}

.header  header {
  height: 77px;
  max-width: 1100px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header img {
  width: 160px;
}


.post {
  margin-bottom: 50px;
    border-radius: 3px;
    border: 1px solid #e6e6e6;
    overflow: hidden;
  }
  
  .post   header {
    display: flex;
    align-items: center;
    background: #fff;
    padding: 16px;
  }
  
  .post header img {
    border-radius: 100%;
    border: 1px solid #bbb;
    width: 32px;
    height: 32px;
    margin-right: 10px;
  }
  
  .post p {
      margin: 5px 15px 10px;
  }
  
  .post-user {
    display: flex;
    flex-direction: column;
  }
  
  .post-user strong {
    font-size: 14px;
    line-height: 18px;
  }
  
  .post-user span {
    font-size: 12px;
    display: block;
  }
  
  .post-image {
    line-height: 0;
    max-height: 400px;
    overflow: hidden;
  }  
  
  .post-image img {
    width: 100%;
    height: 100%;
  }

.post-likes {
  cursor: pointer;
}

.post-likes svg:active {
  transform: scale(1.2);
  color: red
}

.post-likes svg {
  transition: all 0.3s ease;
  font-size: 25px;
  margin: 5px 15px 0;
}

.user-info {
  display: flex;
}

.user-info img {
  margin-right: 10px;
  border-radius: 100%;
  width: 50px;
  height: 50px;
  border: 1px solid #bbb;
}

.user-bio {
  display: flex;
  flex-direction: column;
}

.user-bio  span {
  color: var(--primary);
  font-size: 12px;
}

.stories {
  margin-top: 20px;
  background: #fff;
  border-radius: 3px;
  border: 1px solid #e6e6e6;
  padding: 16px;
}
  
.stories h2 {
  margin: 0;
  font-size: 14px;
  line-height: 18px;
  color: var(--primary);
}

.storie {
  display: flex;
  align-items: center;
  background: #fff;
  padding: 4px 0;
}

.storie-image {
  border: 1px solid #ddd;
  border-radius: 100%;
  width: 40px;
  height: 40px;
  overflow: hidden;
  margin-right: 10px;
}

.storie-image img {
  padding: 2px;
  border-radius: 100%;
  width: 100%;
  height: 100%;
}

.storie-user {
  margin: 5px 0;
  display: flex;
  flex-direction: column;
}

.storie-user strong {
    font-size: 14px;
    line-height: 18px;
}

.storie-user span {
  font-size: 10px;
  display: block;
  color: var(--primary);
  text-transform: uppercase;
}
